<script>

export default {
	name: 'excelUploadLayer',
	data() {
		return {
			tabChange: 0,
			file: null,
			fileName: '',
		};
	},
	components: {

	},
	methods: {
		myFilter: function (event) {
			var myIndex = event.target.parentElement.getAttribute('tab-id');
			var myLi = document.querySelectorAll('.excel_upload .tab_nav > li');
			var myTabContent = document.querySelectorAll('.excel_upload .tab_container > div');
			for (var i = 0; i < myLi.length; i++) {
				myLi[i].classList.remove('active');
				myTabContent[i].classList.remove('active');
			}
			myLi[myIndex].classList.add('active');
			myTabContent[myIndex].classList.add('active');
			this.tabChange = myIndex;
		},
		onFileChange: function (e) {
			var files = e.target.files || e.dataTransfer.files;
			if (files == undefined || files == null || !files.length) return;
			if (!this.checkExcelFile(files[0])) {
				alert('엑셀형식(xls, xlsx) 파일만 업로드 가능합니다.');
				return;
			}

			this.file = files[0];

			if (this.file != undefined && this.file != null) {
				this.fileName = this.file.name;
			}
		},

		closeLayer() {
			this.$emit('changeLayerId', '');
		},
	}
};
</script>