<template>
	<section>
		<div class="item_bundle" v-if="isAd">
			<strong class="tit_bundle">광고</strong>
			<div class="cont_bundle">
				<div class="box_reject_ad">
					<div class="box_add_radio">
						<span class="custom_radio">
							<input type="radio" name="radioAd" id="hiddenAd" v-model="selectType" value="미표시">
							<label for="hiddenAd">미표시</label>
						</span>
						<span class="custom_radio">
							<input type="radio" name="radioAd" id="visibleAd" v-model="selectType" value="표시">
							<label for="visibleAd">표시</label>
						</span>
					</div>
					<div class="box_add_input">
						<strong class="title">무료수신거부</strong>
						<span class="custom_input">
							<input type="text">
						</span>
					</div>
				</div>
			</div>
		</div>

		<div class="item_bundle">
			<strong class="tit_bundle">복사가능여부</strong>
			<div class="cont_bundle">
				<div class="box_reject_ad">
					<div class="box_add_radio">
						<span class="custom_radio">
							<input type="radio" name="radioCa" id="hiddenCa" v-model="selectType2" value="불가능">
							<label for="hiddenCa">불가능</label>
						</span>
						<span class="custom_radio">
							<input type="radio" name="radioCa" id="visibleCa" v-model="selectType2" value="가능">
							<label for="visibleCa">가능</label>
						</span>
						<p class="caution">※ 단말 내 메시지에 대한 복사가능여부 설정입니다. </p>
					</div>
				</div>
			</div>
		</div>

		<div class="item_bundle">
			<strong class="tit_bundle">Fallback</strong>
			<div class="cont_bundle">
				<div class="cont_block_bundle fallback_raido">
					<span class="custom_radio">
						<input type="radio" name="selectSand" id="notUsed" checked>
						<label for="notUsed" v-on:click="fallBackRadio($event)" data-id="0">미사용</label>
					</span>
					<span class="custom_radio">
						<input type="radio" name="selectSand" id="sendSms">
						<label for="sendSms" v-on:click="fallBackRadio($event)" data-id="1">SMS사용</label>
					</span>
					<span class="custom_radio">
						<input type="radio" name="selectSand" id="sendLms">
						<label for="sendLms" v-on:click="fallBackRadio($event)" data-id="2">LMS사용</label>
					</span>
				</div>
				<div class="cont_block_bundle box_textarea active"></div>
				<div class="cont_block_bundle box_textarea">
					<div class="custom_textarea">
						<textarea placeholder="메시지 내용을 입력 해주세요 (최대 90byte)"></textarea>
					</div>
					<!-- 2019.09.18 tooltip 영역 추가-->
					<div class="cont_flex_bundle bottom">
						<p class="caution">※ 최대 90bytes/00 bytes 입력
						<div class="tooltip_area">
							<div class="btn_info"></div>
							<div class="box_tooltip">
								<p>한글 1글자=2byte, <br>그 외 영문/숫자/특수문자/공백 1글자=1byte로 카운트 됩니다.</p>
							</div>
						</div>
						</p>
					</div>
					<p class="caution">※ RCS 메시지 발송 실패 시 SMS나 LMS를 발송할 수 있습니다. </p>
					<p class="caution">※ 변수로 설정하고자 하는 내용을 {{ blank }} 표시로 작성해주세요. <br>예) {{ customerName }}님
						{{ numDate }}출금예정입니다.</p>
				</div>
				<div class="cont_block_bundle box_textarea">
					<!-- 2020.03.05 custom_input 영역 추가 -->
					<span class="custom_input full_width mar_b10">
						<input type="text" placeholder="메시지 제목을 입력 해주세요 (최대 40byte)">
					</span>
					<div class="custom_textarea">
						<textarea placeholder="메시지 내용을 입력 해주세요 (최대 2000byte)" v-model="LMSContText"
							@input="LMSContText = $event.target.value"></textarea>
					</div>
					<!-- 2019.09.18 tooltip 영역 추가-->
					<div class="cont_flex_bundle bottom">
						<p class="caution">※ 최대 2000bytes/00 bytes 입력
						<div class="tooltip_area">
							<div class="btn_info"></div>
							<div class="box_tooltip">
								<p>한글 1글자=2byte, <br>그 외 영문/숫자/특수문자/공백 1글자=1byte로 카운트 됩니다.</p>
							</div>
						</div>
						</p>
					</div>
					<p class="caution">※ RCS 메시지 발송 실패 시 SMS나 LMS를 발송할 수 있습니다. </p>
					<p class="caution">※ 변수 설정 시 위 메시지 내용에 사용한 변수와 동일한 변수명만 사용할 수 있습니다.</p>
				</div>
			</div>
		</div>


		<div class="item_bundle">
			<strong class="tit_bundle">캠페인ID</strong>
			<div class="cont_bundle">
				<span class="custom_input full_width">
					<input type="text" placeholder="캠페인ID를 입력해주세요.">
				</span>
				<p class="caution">※ 발송 메시지의 캠페인 ID는 선택 입력값입니다.</p>
				<p class="caution">※ 영문&#44; 숫자&#44; 특수문자(“ &#46; ” &#44; “ &#45; ” &#44; “ &#64; ”) 입력이 가능합니다.
				</p>
			</div>
		</div> <!-- 20.08.14 추가 -->
	</section>
</template>

<script>

export default {
	name: 'fallbackSetBox',
	props: {
		isAd: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			SMSContText: '',
			LMSContText: '',
			customerName: '{{고객명}}',
			numDate: '{{YYYYMMDD}}',
			blank: '{{ }}',
			selectType: '표시',
			selectType2: '가능',
		};
	},
	components: {

	},
	methods: {
		fallBackRadio: function (event) {
			var myIndex = event.target.getAttribute('data-id');
			var myTabContent = document.querySelectorAll('.cont_block_bundle.box_textarea');
			for (var i = 0; i < myTabContent.length; i++) {
				myTabContent[i].classList.remove('active');
			}
			myTabContent[myIndex].classList.add('active');
		},
	},
	computed: {

	}
};
</script>