<template>
	<div tabindex="0" class="layer big active">
		<div tabindex="0" class="layer_cont">
			<div class="layer_head">
				<h2>주소록</h2>
			</div>
			<div class="layer_body">
				<div class="box_search_area">
					<div class="filter_bundle">
						<span class="custom_input full_width">
							<input type="text" placeholder="이름 또는 휴대폰 번호를 입력해주세요.">
						</span>
						<a href="javascript:void(0);" class="btn mid point"><span>검색</span></a>
					</div>
				</div>
				<div class="search_result addr_layer">
					<div class="tab_wrap">
						<ul class="tab_nav">
							<li class="active" @click="myFilter($event)" tab-id="0"><a href="javascript:void(0);">그룹</a></li>
							<li @click="myFilter($event)" tab-id="1"><a href="javascript:void(0);">전체</a></li>
						</ul>
						<div class="tab_container">
							<div class="tab_content active">
								<h2 class="blind">그룹 탭</h2>

								<addr-group-grid-view v-if="tabChange == 0" />

								<div class="btn_wrap center">
									<a href="javascript:void(0);" class="btn mid" @click="closeLayer"><span>취소</span></a>
									<a href="javascript:void(0);" class="btn mid point"><span>확인</span></a>
								</div>
							</div>
							<div class="tab_content">
								<h2 class="blind">전체 탭</h2>

								<addr-all-grid-view v-if="tabChange == 1" />

								<div class="btn_wrap center">
									<a href="javascript:void(0);" class="btn mid" @click="closeLayer"><span>취소</span></a>
									<a href="javascript:void(0);" class="btn mid point"><span>확인</span></a>
								</div>
							</div>
						</div>
					</div>
				</div>
				<a href="javascript:void(0)" class="btn_close" @click="closeLayer"><span>닫기</span></a>
			</div>
		</div>
	</div>
</template>

<script>
import AddrAllGridView from './AddrAllGridView';
import AddrGroupGridView from './AddrGroupGridView';

export default {
	name: 'addrRecvLayer',
	data() {
		return {
			tabChange: 0,
		};
	},
	components: {
		AddrGroupGridView,
AddrAllGridView,

	},
	methods: {
		myFilter: function(event) {
      var myIndex = event.target.parentElement.getAttribute('tab-id');
      var myLi = document.querySelectorAll('.addr_layer .tab_nav > li');
      var myTabContent = document.querySelectorAll('.addr_layer .tab_container > div');
      for (var i = 0; i < myLi.length; i++) {
        myLi[i].classList.remove('active');
        myTabContent[i].classList.remove('active');
      }
      myLi[myIndex].classList.add('active');
      myTabContent[myIndex].classList.add('active');
      this.tabChange = myIndex;
    },
		closeLayer() {
			this.$emit('changeLayerId', '');
		},
	}
};
</script>